:root {
  --primary-color: #ffd900;
  --secondary-color: #441705;
  --complimentary-color: #007963;
  --contrast-color: #a19700;
  --light-color: #7c0092;
}

body, html {
  margin: 0;
  padding: 0;
  height: 100%;
  overflow: hidden;
  background: var(--primary-color);
}

.app {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-image: url('https://i.imgur.com/F17sOmX.png');
  background-size: cover;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.video-container,
.video-container:target {
  position: relative;
  display: flex;
  width: 100vw;
  justify-content: center;
  align-items: center;
  height: calc(100vh - 50px); /* Reduce height to account for the tab menu */
  overflow: hidden;
}

video {
  width: 100%;
  height: 100%;
  object-fit:contain;
}
img {
  width: 100%;
  height: 100%;
  object-fit:cover;
  
}

.tab-menu {
  position: fixed;
  bottom: 0;  
  left: -10px; /* Shift to the left */
  width: 105%; /* Adjust the width as needed */
  height: 50px;
  background: var(--secondary-color);
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-size: 16px;
  padding: 10px;
  text-align: center;
  box-sizing: border-box; /* Ensure padding doesn't affect the width */
}

.tab {
  flex: 1;
  text-align: center;
  cursor: none;
  transition: all 0.5s ease-in;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ffa600;
}

.tab.active {
  font-size: 25px;
  color: #000;
  padding: 20px;
  background-color: var(--primary-color);
  border-radius: 10px;
}

